import { Component, h } from '@stencil/core';

/**
 * Chip with a badge
 * Chips can display a badge with a number or a short text.
 */
@Component({
    tag: 'limel-example-chip-badge',
    shadow: true,
})
export class ChipBadgeExample {
    public render() {
        return [
            <limel-chip id="a" text="Batman" icon="batman_old" badge={2005} />,
            <span> </span>,
            <limel-chip id="b" text="Batman" icon="batman_new" badge="NEW" />,
        ];
    }
}
